import React from 'react'
import Sun from "../../img/11640520757a8i8cxbzr1yauphoecmamk5uty0epds4aen0d9quvug0yd5febzfw84l50dddkivcknd3yucbyhtc4gud2hewpk6p72ijvqzcw3f.png";
import Moon from "../../img/icon-moon-vector-illustration-yellow-260nw-592979102.png";

import "./toggle.css";
import {useContext} from "react";
import {ThemeContext} from "../../context";


const Toggle =()=>{
	const theme = useContext(ThemeContext)

	const handleClick =() =>{
		theme.dispatch({type: "TOGGLE"})
	}
	return(
		<div className="t">
		<img src={Sun} alt="" className="t-icon"/>
		<img src={Moon} alt="" className="t-icon"/>
		<div className="t-button" onClick ={handleClick} style={{left: theme.state.darkMode ? 0 : 25}}></div>
		</div>
		);
};

export default Toggle;