import React from "react"
import Phone from "../../img/phone-volume-vector-icon-on-260nw-1135845185.png";
import Email from "../../img/kisspng-computer-icons-email-clip-art-5b1643c0644c28.png";
import Address from "../../img/1076323.png";
import {useContext} from 'react';
import {useRef} from 'react';
import {useState} from 'react';
import emailjs from '@emailjs/browser';
import {ThemeContext} from "../../context";
import "./contact.css";

const Contact =()=> {

const formRef = useRef();
const [done,setDone] = useState(false);
const handleSubmit =(e)=>{
	e.preventDefault();

	 emailjs.sendForm('service_ojfow3p', 'template_b2k9udj', formRef.current, 'user_3C04jo8KJMdrjHykBDRsM')
      .then((result) => {
          console.log(result.text);
          setDone(true);
      }, (error) => {
          console.log(error.text);
      });

}
const theme = useContext(ThemeContext);
 const darkMode = theme.state.darkMode;
	return(

		<div className="c">
		<div className="c-bg"></div>
		<div className="c-wrapper">
		<div className="c-left">
			<h1 className="c-title">Let's discuss your project</h1>
			<div className="c-info">
			<div className="c-info-item">
				<img src={Phone} alt ="" className="c-icon"/>
				(+234) 81 6822 9789
			</div>
			<div className="c-info-item">
				<img src={Email} alt ="" className="c-icon"/>
				pjgraphixs@gmail.com
			</div>
			<div className="c-info-item">
				<img src={Address} alt ="" className="c-icon"/>
				Campus Two, Delta State University, Abraka, Delta State.
			</div>
			</div>
		</div>
		<div className="c-right">
			<p className="c-desc">
				<b>What's your story </b>Get in touch. Always ready for freelancing
				if the right projects comes along.
			</p>
		<form ref={formRef} onSubmit={handleSubmit}>
		<input style={{backgroundColor:darkMode && "#333"}} type="text" placeholder="Name" name="user_name" requried/>
		<input style={{backgroundColor:darkMode && "#333"}} type="text" placeholder="Subject" name="user_subject" requried/>
		<input  style={{backgroundColor:darkMode && "#333"}} type="text" placeholder="Email" name="user_email" requried/>
		<textarea style={{backgroundColor:darkMode && "#333"}} rows="5" placeholder="Message" name="message" requried/>
		<button>Submit</button>
		{done && "Thank you... We will get back to you soonest"}
		</form>
		</div>
		</div>
		</div>
		);
}

export default Contact;